import React from 'react'
import { useApp } from '../../context/Context';
import styled from 'styled-components';
import textContext from '../../textContext';

// Hook for checking viewport + animation package
import { useInView } from 'react-hook-inview';
import SmoothList from 'react-smooth-list';

// Comps
import TokenList from './TokenList';
import { aptBG, aptLogo } from '../../assets/images';

const Background = styled('div')`
	height: 100%;
	width: 100%;
	background-image: url(${aptBG});
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	margin-top: 7.5rem;
	.textContainer {
		padding-top: 0;
	}
	@media (max-width: 900px) {
		margin-top: 2rem;
	}
`;

const Title = styled('p')`
	&:before {
		position: absolute;
		content: '';
		z-index: -1;
		top: -60px;
		right: 0;
		left: -15px;
		height: 107.5px;
		width: 109.5px;
		margin: auto;
		background-image: url(${aptLogo});
		background-position: center;
		background-size: contain;
		background-repeat: no-repeat;
		@media (max-width: 900px) {
			height: 45px;
			width: 100%;
			top: -20px;
			left: 0;
		}
	}
`;

export default function Token() {

	const { language, width } = useApp();
  const text = textContext[language].token;

	const [view, setView] = React.useState(false);
  const [ref, isVisible] = useInView({
    threshold: width < 500 ? 0.5 : 0,
  })

  // Trigger setView on viewport enter
  React.useEffect(() => {
  	if (width < 900) {
  		setTimeout(() => {
  			isVisible ? setView(true) : setView(false)
  		}, 500)
  	} else {
  		isVisible ? setView(true) : setView(false)
  	}
  }, [isVisible, width])

	return (
		<Background id="token">
			<div ref={ref} className="padding textContainer">
				<Title className="textHeader">{text.title}</Title>
				<div className="content">
					<p>{text.body}</p>
{/*					<h2>
						<span>{text.contentOne}</span> <span>{text.contentTwo}</span>
					</h2>*/}
				</div>
				<SmoothList transitionDuration={1000} delay={100} visible={view} className="tokenList">
					{
						text.list.map((l,i) => (
							<TokenList key={i} l={l} />
						))
					}
				</SmoothList>
			</div>
		</Background>
	)
}